.TemplateSuccess {
  font-family: "Roboto";
}

label {
  position: unset;
}

input {
  margin: 10px auto;
  padding-left: 15px;
  padding-top: 0;
  font-size: 14px;
  border-radius: 4px;
}

.Input_Container {
  margin-top: 25px;
}

.TemplateSuccess .Element_Container {
  background-color: rgb(0 0 0 / 46%);
  width: 90%;
  margin: 20px auto;
  padding: 15px;
  box-shadow: 3px 4px 8px #e1e1e1;
  border-radius: 4px;
}

main form div {
  position: unset;
  width: 100%;
  margin-bottom: 5px;
  display: block;
}

button {
  margin-top: 10px;
  margin-left: 0;
}
